body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(
    to left,
    rgba(7, 27, 82, 1) 0%,
    rgba(0, 128, 128, 1) 100%
  );
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Stuart Little Secondary Regular";
  font-style: normal;
  font-weight: normal;
  src: local("Stuart Little Secondary Regular"),
    url(/robo-friend-publish/static/media/StuartLittleSecondary-8MD2D.f8c7aabe.woff) format("woff");
}

h1 {
  font-family: Stuart Little Secondary Regular;
  font-weight: 200;
  color: lightblue;
}

